<template>
  <div class="qrcode">
    <div class="header">
      <div class="left">
        <img
          src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/Qrcode/title.png"
          alt=""
          class="title" />
        <div class="tips">班主任会在 <span>24小时</span>内通过好友申请</div>
      </div>
      <div class="right">
        <img
          src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/Qrcode/ht.png"
          alt=""
          class="ht" />
      </div>
    </div>
    <div class="content">
      <div
        class="item"
        v-for="(v, k) in classInfo"
        :key="k">
        <div class="qr" v-if="k == 0 || showAll">
          <div class="qr_title">{{ className[v.sp] }}</div>
          <div
            class="wrap"
            v-for="item in v.sucess"
            :key="item.order_num">
            <div class="info">
              <div class="info_title">班主任二维码</div>
              <img
                :src="item.result.qrcode"
                alt=""
                v-log:long="{
                  options: {
                    module_ori: '课程卡片',
                    module_part: '106_tf_h5_00060',
                    alternate: JSON.stringify({
                      pageName: '微淼小课公众号分班-老师二维码页',
                      blockname: className[v.sp],
                    }),
                  },
                }"
                class="info_qr" />
              <div class="info_tip">点击二维码，长按添加班主任</div>
            </div>
            <div class="detail">
              <div class="buy_time">
                购买时间：{{ item.create_time || "--" }}
              </div>
              <div class="fb_time">
                分班时间：{{ item.join_class_time || "--" }}
              </div>
            </div>
          </div>
          <div
            :class="{
              err_info: true,
              active: v.fail.length > 0 && v.sucess.length > 0,
            }"
            v-if="v.fail.length >= 0">
            <div
              class="item"
              v-for="item in v.fail"
              :key="item.order_num">
              <div class="order">
                <img
                  src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/Qrcode/warn.png"
                  alt=""
                  class="warn" />
                <span class="order_num">订单号:{{ item.order_num }}</span>
                <span
                  class="copy"
                  @click="
                    copy(item.order_num, {
                      pageName: '微淼小课公众号分班-老师二维码页',
                      blockname: className[v.sp],
                      buttonname: item.result.msg,
                    })
                  ">复制</span>
              </div>
              <div class="order_info">
                {{ item.result.msg }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="warn_text">已退款课程，不再显示老师二维码</div>
    <div
      class="showAll"
      @click="showAllList"
      v-if="!showAll && classInfo.length > 1">
      查看以前老师
      <img
        class="down"
        src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/Qrcode/down.png"
        alt="" />
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Store from '@/store'
import { copyText, getSearch } from '@/utils'
import wxAuth from '@/helper/Auth/wxAuth'
export default {
  data () {
    return {
      list: {},
      showAll: false
    }
  },
  computed: {
    ...mapState('user', ['className', 'classInfo'])
  },
  beforeRouteLeave (to, from, next) {
    if (to.name !== 'CheckPhone') {
      next({
        name: 'CheckPhone'
      })
    }
    next()
  },
  async beforeRouteEnter (to, from, next) {
    await wxAuth({
      getTokenQuery: { ...getSearch() }
    })
    const { id } = Store.state.user.userInfo || {}
    Store.$log.update({ mid: id })
    next()
  },
  created () {
    wx.ready(() => {
      wx.hideAllNonBaseMenuItem()
    })
  },
  methods: {
    showAllList () {
      this.$log.click({
        module_part: '106_tf_h5_00062',
        module_ori: '其他模块',
        alternate: JSON.stringify({
          pageName: '微淼小课公众号分班-老师二维码页'
        })
      })
      this.showAll = true
    },
    copy (orderNum, data) {
      this.$log.click({
        module_ori: '课程卡片',
        module_part: '106_tf_h5_00061',
        alternate: JSON.stringify(data)
      })
      copyText(orderNum, '复制成功')
    }
  }
}
</script>

<style lang="scss" scoped>
.qrcode {
  width: 750px;
  padding-bottom: 20px;
  background: linear-gradient(360deg, #2432ac 0%, #344de0 98%);
  min-height: 100vh;
  .showAll {
    height: 45px;
    font-size: 32px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #ffffff;
    line-height: 45px;
    display: flex;
    justify-content: center;
    margin-bottom: 100px;
    .down {
      width: 40px;
      height: 40px;
    }
  }
  .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 626px;
    height: 107px;
    background: linear-gradient(180deg, #ffffff 0%, #f4f5ff 31%, #aebaff 100%);
    border-radius: 54px;
    font-size: 36px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #1d2c99;
    line-height: 107px;
    margin: 0 auto;
    .right {
      height: 25px;
      width: 15px;
      margin-left: 30px;
    }
  }
  .warn_text {
    width: 420px;
    height: 40px;
    margin: 20px auto;
    font-size: 28px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #e5e8ff;
    line-height: 40px;
    margin-bottom: 20px;
  }
  .content {
    .item {
      margin-bottom: 50px;
      .qr {
        width: 670px;
        padding-bottom: 40px;
        margin: 0 auto;
        background: #ffffff;
        border-radius: 10px;
        position: relative;
        padding-top: 100px;
        padding-left: 20px;
        padding-right: 20px;
        .active {
          // TODO
          border-top: 1px dashed #dce1e8;
        }
        .err_info {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-top: 30px;
          padding: 30px;
          .item {
            width: 600px;
            height: 205px;
            background: #f8f8f7;
            border-radius: 12px;
            padding: 32px;
            margin-top: 20px;
            .order_info {
              height: 80px;
              font-size: 28px;
              font-family: PingFangSC-Regular, PingFang SC;
              font-weight: 400;
              color: #999999;
              line-height: 40px;
              padding-left: 44px;
              margin-top: 20px;
            }
            .order {
              height: 45px;
              font-size: 32px;
              font-weight: 400;
              color: #333333;
              font-family: PingFangSC-Regular, PingFang SC;
              line-height: 45px;
              display: flex;
              align-items: center;
              .warn {
                width: 36px;
                height: 36px;
                margin-right: 10px;
              }
              .order_num {
                flex-grow: 1;
                @include ellipsis;
              }
              .copy {
                width: 106px;
                margin-left: 20px;
                height: 40px;
                font-size: 28px;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: #1e66ed;
                line-height: 40px;
              }
            }
          }
        }
        .detail {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-top: 20px;
          div {
            // width: 410px;
            height: 40px;
            font-size: 28px;
            font-weight: 400;
            color: #999999;
            line-height: 40px;
          }
        }
        .info {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-evenly;
          width: 457px;
          height: 554px;
          background: #f7f7f8;
          border-radius: 12px;
          margin: 0 auto;
          .info_title {
            font-size: 36px;
            font-weight: 600;
            color: #2a2f36ff;
          }
          .info_tip {
            font-size: 28px;
            font-weight: 400;
            color: #666666;
            line-height: 40px;
          }
          .info_qr {
            width: 352px;
            height: 352px;
          }
        }
        .qr_title {
          width: 547px;
          height: 88px;
          background-image: url("https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/Qrcode/title_bg.png");
          background-size: contain;
          background-repeat: no-repeat;
          position: absolute;
          left: 50%;
          top: -20px;
          transform: translateX(-50%);
          font-size: 40px;
          font-weight: 500;
          color: #ffffff;
          line-height: 88px;
          text-align: center;
        }
      }
    }
  }
  .header {
    display: flex;
    height: 390px;
    justify-content: space-between;
    .left {
      padding-top: 87px;
      padding-left: 65px;
      .title {
        width: 280px;
        height: 131px;
      }
      .tips {
        margin-top: 15px;
        width: 407px;
        height: 48px;
        background: #2432ac;
        border-radius: 4px;
        font-size: 24px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #ffffff;
        line-height: 48px;
        text-align: center;
        span {
          color: #ffd546;
        }
      }
    }
    .right {
      padding-top: 45px;
      padding-right: 11px;
      .ht {
        width: 206px;
        height: 272px;
      }
    }
  }
}
</style>
